const Button: React.FC<ButtonProps> = ({
  label,
  color,
  handleClick,
  p,
  icon,
}) => (
  <button
    className={classNames('button', {
      [`button--${color}`]: color,
      [`p-${p}`]: p,
    })}
    onClick={handleClick}
    aria-label={label || 'Toggle theme'}
  >
    {icon && <span className="button__icon">{icon}</span>}
    {label && <span className="button__label">{label}</span>}
  </button>
);
